.sidebar_cart {
    height: 800px;
    width: 330px;
    background-color: #d7d7d7;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 555; 
}

.header_cart {
    width: 100%;
}
.icons_cart {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    opacity: .7;
}
.line_cart {
    height: 1px;
    background-color: black;
}
.clear_cart {
    cursor: pointer;
}
.content_cart {
    height: 70%;
    overflow-y: auto;
}
.card_in_cart {
    display: flex;
    justify-content: center;
    gap: 1rem;
    align-items: center;
    margin-top: 10px;
}
.card_in_cart img{
    width: 100px;
}
.description_of_item h1 {
    font-size: 18px;
    font-weight: bolder;
}
.description_of_item {
    margin-top: 15px;   
}
.description_of_item p {
    color: #574e4e;
}
.sum {
    font-size: 19px;
    color: #aaaaaa;
}
.sum span {
    color: rgb(60, 60, 243);
}

.line_cart2 {
    height: 1px;
    background-color: rgb(118, 118, 118);

}
.items_sum_cart {
    width: 100%;
    height: 67px;
    background-color: #d7d7d7;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right: 20px;
    padding-left: 20px;
    font-size: 18px;

}
.cart_btn {
    height: 67px;
    background-color: #feb600;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    color: rgb(48, 48, 48);
    font-size: 16;
    cursor: pointer;
}
.sum_money {
    color: rgb(60, 60, 243);
    
}